"use client";

import { useMemo, useState } from "react";
import { MapPin, ArrowUpDown } from "lucide-react";
import { Sale } from "./Sale";
import jsPDF from "jspdf";
import autoTable from "jspdf-autotable";


type SortKey =
    | "name"
    | "saleCount"
    | "quantity"
    | "totalTonnage"
    | "totalSalesValue"
    | "avgOrderValue"
    | "contribution";

interface Props {
    sales: Sale[];
    title: string;
    iconColor: string;
    limit?: number;
    startDate?: Date;
    endDate?: Date;
}

export default function SalesByLocation({
    sales,
    title,
    iconColor,
    limit = 10,
}: Props) {
    const [sortKey, setSortKey] = useState<SortKey>("totalSalesValue");
    const [sortDir, setSortDir] = useState<"asc" | "desc">("desc");

    // Date filter state
    const [startDate, setStartDate] = useState<Date | null>(null);
    const [endDate, setEndDate] = useState<Date | null>(null);

    // Filter sales by selected date range
    const filteredSales = useMemo(() => {
        return sales.filter(sale => {
            const saleDate = new Date(sale.saleDate);
            if (startDate && saleDate < startDate) return false;
            if (endDate && saleDate > endDate) return false;
            return true;
        });
    }, [sales, startDate, endDate]);

    // Aggregate filtered sales
    const data = useMemo(() => {
        const map: Record<
            string,
            {
                id: string;
                name: string;
                saleCount: number;
                quantity: number;
                totalTonnage: number;
                totalSalesValue: number;
                avgOrderValue: number;
                contribution: number;
            }
        > = {};

        filteredSales.forEach(sale => {
            const id = sale.locationId;
            const name = sale.location?.name || "Unknown";

            if (!map[id]) {
                map[id] = {
                    id,
                    name,
                    saleCount: 0,
                    quantity: 0,
                    totalTonnage: 0,
                    totalSalesValue: 0,
                    avgOrderValue: 0,
                    contribution: 0,
                };
            }

            const salesValue = sale.totalAmount ?? sale.salePrice * sale.quantity;

            const weight = sale.product?.weightValue || 0;
            const packSize = sale.product?.packSize || 1;
            const tonnage = (weight * sale.quantity * packSize) / 1000;

            map[id].saleCount += 1;
            map[id].quantity += sale.quantity;
            map[id].totalSalesValue += salesValue;
            map[id].totalTonnage += tonnage;
        });

        const totalRevenue = Object.values(map).reduce(
            (sum, loc) => sum + loc.totalSalesValue,
            0
        );

        return Object.values(map).map(loc => ({
            ...loc,
            avgOrderValue: loc.saleCount > 0 ? loc.totalSalesValue / loc.saleCount : 0,
            contribution: totalRevenue > 0 ? (loc.totalSalesValue / totalRevenue) * 100 : 0,
        }));
    }, [filteredSales]);


    const sortedData = useMemo(() => {
        return [...data]
            .sort((a, b) => {
                const aVal = a[sortKey];
                const bVal = b[sortKey];

                if (typeof aVal === "string") {
                    return sortDir === "asc"
                        ? aVal.localeCompare(bVal as string)
                        : (bVal as string).localeCompare(aVal);
                }

                return sortDir === "asc"
                    ? (aVal as number) - (bVal as number)
                    : (bVal as number) - (aVal as number);
            });
    }, [data, sortKey, sortDir, limit]);

    const toggleSort = (key: SortKey) => {
        if (key === sortKey) {
            setSortDir(sortDir === "asc" ? "desc" : "asc");
        } else {
            setSortKey(key);
            setSortDir("desc");
        }
    };

    const Header = ({
        label,
        column,
        align = "left",
    }: {
        label: string;
        column: SortKey;
        align?: "left" | "right";
    }) => (
        <th
            onClick={() => toggleSort(column)}
            className={`py-2 px-3 cursor-pointer select-none border-r hover:text-blue-600 ${align === "right" ? "text-right" : "text-left"
                }`}
        >
            <span className="inline-flex items-center gap-1">
                {label}
                <ArrowUpDown className="w-3 h-3 opacity-60" />
            </span>
        </th>
    );

    // Export to csv
    const exportCSV = () => {
        const headers = [
            "Location",
            "Orders",
            "Quantity",
            "Tonnage",
            "Avg Order Value",
            "Revenue",
            "Contribution %",
        ];

        const rows = sortedData.map(l => [
            l.name,
            l.saleCount,
            l.quantity,
            l.totalTonnage.toFixed(2),
            l.avgOrderValue.toFixed(2),
            l.totalSalesValue.toFixed(2),
            l.contribution.toFixed(2),
        ]);

        const csv = [headers, ...rows]
            .map(r => r.join(","))
            .join("\n");

        const blob = new Blob([csv], { type: "text/csv" });
        const url = URL.createObjectURL(blob);

        const a = document.createElement("a");
        a.href = url;
        a.download = "sales_by_location.csv";
        a.click();

        URL.revokeObjectURL(url);
    };

    // Inside your component
    const exportPDF = (startDate?: Date, endDate?: Date) => {
        const doc = new jsPDF(); // portrait

        // Format dates
        const start = startDate ? startDate.toLocaleDateString() : "N/A";
        const end = endDate ? endDate.toLocaleDateString() : "N/A";

        // Title at the top
        doc.setFontSize(16);
        doc.setFont("helvetica", "bold");
        doc.text(title, doc.internal.pageSize.getWidth() / 2, 15, { align: "center" });

        // Date range below title
        doc.setFontSize(10);
        doc.setFont("helvetica", "normal");
        doc.text(`Date Range: ${start} - ${end}`, doc.internal.pageSize.getWidth() / 2, 23, { align: "center" });

        // Totals calculation
        const totals = {
            saleCount: sortedData.reduce((sum, d) => sum + d.saleCount, 0),
            quantity: sortedData.reduce((sum, d) => sum + d.quantity, 0),
            totalTonnage: sortedData.reduce((sum, d) => sum + d.totalTonnage, 0),
            totalSalesValue: sortedData.reduce((sum, d) => sum + d.totalSalesValue, 0),
        };
        totals.avgOrderValue = totals.saleCount > 0 ? totals.totalSalesValue / totals.saleCount : 0;

        // Table
        autoTable(doc, {
            startY: 30, // start a bit lower so title/date are visible
            head: [[
                "#", "Location", "Orders", "Quantity", "Tonnage (t)",
                "Avg Order Value", "Revenue", "% Share"
            ]],
            body: sortedData.map((loc, index) => [
                index + 1,
                loc.name,
                loc.saleCount,
                loc.quantity,
                loc.totalTonnage.toFixed(2),
                `K${loc.avgOrderValue.toFixed(0)}`,
                `K${loc.totalSalesValue.toFixed(0)}`,
                `${loc.contribution.toFixed(1)}%`
            ]),
            foot: [[
                "Total",
                "-",
                totals.saleCount,
                totals.quantity,
                totals.totalTonnage.toFixed(2),
                `K${totals.avgOrderValue.toFixed(0)}`,
                `K${totals.totalSalesValue.toFixed(0)}`,
                "100%"
            ]],
            styles: { fontSize: 9, cellPadding: 4 },
            headStyles: { fillColor: [100, 100, 100], fontStyle: "bold", textColor: 255 },
            footStyles: { fillColor: [100, 100, 100], fontStyle: "bold", textColor: 255 },
            alternateRowStyles: { fillColor: [245, 245, 245] },
        });

        doc.save("sales_by_location.pdf");
    };

    return (
        <div className="bg-white p-6 rounded-xl border hover:shadow-md transition-shadow">
            <h3 className="font-semibold mb-4 flex items-center gap-2">
                <MapPin className={`h-5 w-5 ${iconColor}`} />
                {title}
            </h3>
            <div className="flex flex-wrap justify-between items-center mb-4">
                {/* Left side: Date pickers */}
                <div className="flex gap-2 items-center rounded border bg-gray-50 px-2 py-1">
                    <label className="flex items-center gap-2 font-medium">
                        Start Date:
                        <input
                            type="date"
                            value={startDate ? startDate.toISOString().substring(0, 10) : ""}
                            onChange={(e) =>
                                setStartDate(e.target.value ? new Date(e.target.value) : null)
                            }
                            className="border rounded px-2 py-1 text-sm hover:bg-gray-200 cursor-pointer"
                        />
                    </label>

                    <label className="flex items-center gap-2 font-medium">
                        End Date:
                        <input
                            type="date"
                            value={endDate ? endDate.toISOString().substring(0, 10) : ""}
                            onChange={(e) =>
                                setEndDate(e.target.value ? new Date(e.target.value) : null)
                            }
                            className="border rounded px-2 py-1 text-sm hover:bg-gray-200 cursor-pointer "
                        />
                    </label>
                </div>

                {/* Right side: Export buttons */}
                <div className="flex gap-2 items-center">
                    <button
                        onClick={exportCSV}
                        className="text-sm px-3 py-1 rounded border font-medium hover:bg-gray-100"
                    >
                        Export CSV
                    </button>

                    <button
                        onClick={() => exportPDF(startDate ?? undefined, endDate ?? undefined)}
                        className="text-sm px-3 py-1 rounded border font-medium hover:bg-gray-100"
                    >
                        Export PDF
                    </button>
                </div>
            </div>
            {sortedData.length > 0 && (
                <div className="max-h-[420px] overflow-y-auto">
                    <table className="w-full text-sm border border-gray-200 border-collapse">
                        <thead className="sticky top-0 bg-gray-200 z-10 whitespace-nowrap">
                            <tr className="text-gray-600">
                                <th className="py-2 px-3 border-r text-left">#</th>
                                <Header label="Location" column="name" />
                                <Header label="Orders" column="saleCount" align="right" />
                                <Header label="Quantity" column="quantity" align="right" />
                                <Header label="Tonnage" column="totalTonnage" align="right" />
                                <Header label="Avg Order Value" column="avgOrderValue" align="right" />
                                <Header label="Revenue" column="totalSalesValue" align="right" />
                                <Header label="% Share" column="contribution" align="right" />
                            </tr>
                        </thead>

                        <tbody>
                            {sortedData.map((loc, index) => (
                                <tr
                                    key={loc.id}
                                    className={`${index % 2 === 0 ? "bg-white" : "bg-gray-50"} hover:bg-blue-50`}
                                >
                                    <td className="py-2 px-3 border-r">{index + 1}</td>
                                    <td className="py-2 px-3 border-r font-medium truncate">{loc.name}</td>
                                    <td className="py-2 px-3 border-r text-center font-small">{loc.saleCount}</td>
                                    <td className="py-2 px-3 border-r text-center font-small">{loc.quantity}</td>
                                    <td className="py-2 px-3 border-r text-center font-small">{loc.totalTonnage.toFixed(2)}</td>
                                    <td className="py-2 px-3 border-r text-center font-small">K{loc.avgOrderValue.toFixed(0)}</td>
                                    <td className="py-2 px-3 text-center font-small">K{loc.totalSalesValue.toFixed(0)}</td>
                                    <td className="py-2 px-3 border-r text-center font-small">{loc.contribution.toFixed(1)}%</td>
                                </tr>
                            ))}

                            {/* TOTAL ROW */}
                            <tr className="bg-gray-200 font-bold">
                                <td className="py-2 px-3 border-r text-left">Total</td>
                                <td className="py-2 px-3 border-r">-</td>
                                <td className="py-2 px-3 border-r text-center">{sortedData.reduce((sum, d) => sum + d.saleCount, 0)}</td>
                                <td className="py-2 px-3 border-r text-center">{sortedData.reduce((sum, d) => sum + d.quantity, 0)}</td>
                                <td className="py-2 px-3 border-r text-center">{sortedData.reduce((sum, d) => sum + d.totalTonnage, 0).toFixed(2)}</td>
                                <td className="py-2 px-3 border-r text-center">
                                    K{(sortedData.reduce((sum, d) => sum + d.totalSalesValue, 0) / Math.max(sortedData.reduce((sum, d) => sum + d.saleCount, 0), 1)).toFixed(0)}
                                </td>
                                <td className="py-2 px-3 border-r text-center">
                                    K{sortedData.reduce((sum, d) => sum + d.totalSalesValue, 0).toFixed(0)}
                                </td>
                                <td className="py-2 px-3 border-r text-center">100%</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            )}
        </div>
    );
}
